<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <v-app>
      <v-row>
        <v-col>
          <v-row>

            <!-- VENDAS EM ABERTO -->
            <v-col cols="12" sm="6" lg="5">
              <v-card elevation="10">
                <v-card-title>Vendas em aberto</v-card-title>
                <v-data-table
                  dense
                  v-model="selected"
                  show-select
                  :items-per-page="5"
                  :headers="headersVendas"
                  :items="listaVendas"
                  :search="search"
                  class="elevation-1"
                  :loading="loading"
                  loading-text="Carregando..."
                  height="195px"
                  :item-class="itemRowBackground"
                >
                  <template v-slot:top>
                    <v-toolbar flat color="white">
                      <v-toolbar-title>Vendas</v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-text-field v-model="search" append-icon="mdi-magnify" label="Buscar"></v-text-field>
                      <v-spacer></v-spacer>
                      <v-btn class="mb-2" color="warning" dark @click="listarVendas"
                        ><v-icon>cached</v-icon></v-btn
                      >
                    </v-toolbar>
                  </template>

                  <template v-slot:item.paga="{ item }">
                    <v-chip
                        class="ma-0"
                        :color="item.paga ? 'green' : 'red'"
                        text-color="white"
                        small
                      >
                        {{item.paga ? 'Sim' : 'Não'}}
                    </v-chip>
                  </template>

                  <template v-slot:item.total="{ item }">
                      $ {{item.total.toFixed(2)}}
                    </template>

                  <!-- <template v-slot:item.acoes="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)"
                      >mdi-pencil</v-icon
                    >
                    <v-icon small color="danger" @click="deleteItem(item)"
                      >mdi-delete</v-icon
                    >
                  </template> -->

                  <template v-slot:no-data>
                    <v-btn color="primary" @click="iniciar">Reiniciar</v-btn>
                  </template>
                </v-data-table>
              </v-card>
            </v-col>
            
            <!-- VENDAS SELECIONADAS -->
            <v-col cols="12" sm="6" lg="4">
              <v-card v-if="selected.length" elevation="10" >
                <v-card-title>Vendas Selecionadas</v-card-title>
                <v-data-table
                  dense
                  :items-per-page="5"
                  :headers="headersVendasSelecionadas"
                  :items="selected"
                  :search="search"
                  class="elevation-1"
                  :loading="loading"
                  loading-text="Carregando..."
                  height="195px"
                >
                  <template v-slot:top>
                    <v-toolbar flat color="white">
                      <v-toolbar-title></v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                      <!-- <v-btn class="mb-2" color="warning" dark @click="iniciar"
                        ><v-icon>cached</v-icon>
                      </v-btn> -->
                    </v-toolbar>
                  </template>

                  <!-- <template v-slot:item.acoes="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)"
                      >mdi-pencil</v-icon
                    >
                    <v-icon small color="danger" @click="deleteItem(item)"
                      >mdi-delete</v-icon
                    >
                  </template> -->

                  <template v-slot:no-data>
                    <v-btn color="primary" @click="iniciar">Reiniciar</v-btn>
                  </template>
                </v-data-table>
              </v-card>
            </v-col>


            <v-col cols="12" lg="3" v-if="selected.length" id="imprimir" class="imprimir">
              <v-card elevation="10" color="#26c6da">
                    <v-card-title>Total</v-card-title>
                    <v-card-text>
                      <v-row>
                        <v-col cols="6">
                        A receber  
                        <h4 :class="`${(totalSelecionado == totalRecebido) ? 'verde' : 'vermelho'}`">
                          {{ totalSelecionado | dolar }}
                        </h4>
                      </v-col>
                      <v-col cols="6">
                        Recebido:
                        <h4 :class="`${(totalSelecionado == totalRecebido) ? 'verde' : 'vermelho'}`">
                          {{ totalRecebido | dolar }}
                        </h4>
                        <span style="font-size:12px;"
                          :class="`${(totalDiferenca <= 0 ) ? 'verde' : 'vermelho'}`"
                        >
                          {{(totalDiferenca > 0 ) ? 'Diferença' : 'Troco'  }}: US$ {{ totalDiferenca }}
                        </span>
                      </v-col>
                      </v-row>
                    </v-card-text>
                  </v-card>
              


              <v-card elevation="10" color="#26c6da" dark>
                <!-- <v-card-title>Total outras moedas</v-card-title> -->
                <v-card-text>
                 <v-row>
                   <v-col cols="6"> 
                      A receber  
                      <h4>{{ (totalSelecionadoPrincipaisMoedas.real) | real }}</h4>
                      <h4>{{ (totalSelecionadoPrincipaisMoedas.guarani) | guarani }}</h4>
                      <h4>{{ (totalSelecionadoPrincipaisMoedas.peso) | peso }}</h4>
                   </v-col>
                   <v-col cols="6" :class="`${(totalDiferenca >=0 ) ? 'verde' : 'vermelho'}`"> 
                      {{(totalDiferenca > 0 ) ? 'Diferença' : 'Troco'  }}
                      <h4 :class="`${(totalDiferenca >=0 ) ? 'verde' : 'vermelho'}`">{{ (totalDiferencaPrincipaisMoedas.real) | real }}</h4>
                      <h4 :class="`${(totalDiferenca >=0 ) ? 'verde' : 'vermelho'}`">{{ (totalDiferencaPrincipaisMoedas.guarani) | guarani }}</h4>
                      <h4 :class="`${(totalDiferenca >=0 ) ? 'verde' : 'vermelho'}`">{{ (totalDiferencaPrincipaisMoedas.peso) | peso }}</h4>
                   </v-col>
                 </v-row>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>



          <v-row>
            <v-col cols="12" sm="6" md="5">
              <v-card height="300" v-if="selected.length" elevation="10" >
                <v-card-title>Informar o valor </v-card-title>
                <v-card-text>
                  <v-row>
                    <v-col cols="12" md="4">
                      <v-select v-model="moeda.id" :items="moedas" label="Moedas" @change="irValorRecebido" ref="select_moeda">
                      </v-select>
                    </v-col>
                    <v-col cols="6" md="3">
                      <v-text-field v-model="valorRecebido" label="Valor" @keyup="calculaValorRecebido" @keypress.enter="adicionarLancamento" type="number" ref="valorRecebido"></v-text-field>
                    </v-col>
                    <v-col cols="6" md="3">
                      <v-text-field v-model="valorConvertido" label="Valor em U$" disabled=""></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <div class="d-grid gap-2">
                      <v-btn color="primary" @click="adicionarLancamento">+</v-btn>
                      </div>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-col>

            <v-col cols="12" sm="6" md="7">
              <v-card v-if="lancamentos.length">
                <!-- <v-card-title>Valores recebidos</v-card-title> -->
                <v-data-table
                  dense
                  :items-per-page="5"
                  :headers="headersLancamentos"
                  :items="lancamentos"
                  :search="search"
                  class="elevation-1"
                  :loading="loading"
                  loading-text="Carregando..."
                  height="175px"
                >
                  <template v-slot:top>
                    <v-toolbar flat color="white">
                      <v-toolbar-title>Lançamentos no Caixa</v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                      <!-- <v-btn class="mb-2" color="warning" dark @click="iniciar"
                        ><v-icon>cached</v-icon>
                      </v-btn -->
                      >
                    </v-toolbar>
                  </template>

                  <template v-slot:item.acoes="{ item }">
                    <!-- <v-icon small class="mr-2" @click="editItem(item)"
                      >mdi-pencil</v-icon
                    > -->
                    <v-icon small color="danger" @click="deleteItem(item)"
                      >mdi-delete</v-icon
                    >
                  </template>

                  <template v-slot:no-data>
                    <v-btn color="primary" @click="iniciar">Reiniciar</v-btn>
                  </template>
                </v-data-table>
              </v-card>
            </v-col>  
          </v-row>
          <v-row>
            <v-col>
            <v-card class="pl-4">
              <v-row>
            <v-col cols="6" sm="3" v-if="enableButton()">
               <div class="d-grid gap-2">
                 <v-btn class="mb-2" color="success" dark @click="salvar">
                   <v-icon>check</v-icon>Gravar
                 </v-btn>
               </div>
            </v-col>
            <v-col cols="6" sm="3">
               <div class="d-grid gap-2">
                 <v-btn class="mb-2" color="error" dark>
                   <v-icon>mdi-close</v-icon>Cancelar
                 </v-btn>
               </div>
            </v-col>
            <v-col cols="6" sm="3">
               <div class="d-grid gap-2">
                 <v-btn class="mb-2" color="primary" dark @click="dialogTicket=!dialogTicket">
                   <v-icon>mdi-print</v-icon>Imprimir
                 </v-btn>
               </div>
            </v-col>
            <v-col cols="6" sm="3">
               <div class="d-grid gap-2">
                 <v-btn class="mb-2" color="primary" dark @click="modalTicketShow=!modalTicketShow">
                   <v-icon>mdi-print</v-icon>Imprimir 2
                 </v-btn>
               </div>
            </v-col>
            </v-row>
            </v-card>
            </v-col>
          </v-row>
        </v-col>
      </v-row>




<!--       <template id="imprimir">
  <div class="text-center imprimir">
 -->    <v-dialog
      v-model="dialogTicket"
      width="500"
      class="imprimir"
      id="tique"
    >
      <!-- <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="red lighten-2"
          dark
          v-bind="attrs"
          v-on="on"
        >
          Click Me
        </v-btn>
      </template> -->

      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Privacy Policy
        </v-card-title>

        <v-card-text >
          <span v-for="lancamento in lancamentos" :key="lancamento.id">
            {{lancamento.descricao}}
          </span>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="imprimir"
          >
            Imprimir
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="dialogTicket = false"
          >
            Fechar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  <!-- </div>
</template> -->








    


<!-- impressão ticket -->
   
<b-modal 
  v-model="modalTicketShow" 
  size="sm"
  ok-only
  ok-variant="info"
  ok-title="Sair"
>
  <div class="noPrint">
    <b-btn @click="imprimir"><v-icon>print</v-icon></b-btn> 
  </div>
  <div id="facturaTicket">
    <header>
      <div id="logo">
        <img
          src="data:image/png;base64,"
          id="imagen"
        />
      </div>
      <div id="datos">
        <p id="encabezado">
          <b>Infinity Group S.A.</b><br />Av. San Blás, 136 - Ciudad del
          Este, Paraguay<br />Telefono:(+595)512216<br />Email:infinity@gmail.com
        </p>
      </div>
      <div id="factTicket">
        <p>
          Ticket Nº: 123456789 <br>
          Data: {{data}}
          <!-- {{ tipo_comprobante }}<br />
          {{ serie_comprobante }}-{{ num_comprobante }}<br />
          {{ fecha }} -->
        </p>
      </div>
    </header>
    <br />
    <section>
      <div>
        <!-- <table id="facliente">
          <tbody>
            <tr>
              <td id="cliente"> -->
                <!-- <strong>Cliente: </strong
                ><br />
                <strong>Documento: </strong> <br />
                <strong>Endereço: Rua das Amélias</strong> <br />
                <strong>Telefone: </strong><br />
                <strong>Email: </strong> -->
              <!-- </td>
            </tr>
          </tbody>
        </table> -->
        <strong 
          class="tituloNegritoCentralizado" 
          >COMPROVANTE DE PAGAMENTO</strong>
      </div>
    </section>
    
    
    
    <br />
    <section>
      <div class="negrito" >
        <span>DESCRIÇÃO</span>
        <span style="margin-left: 30px;"></span><br>
        <span>MOEDA'</span>
        <span style="margin-left: 15px;">VALOR</span>
        <span style="margin-left: 15px;">COTAÇÃO</span>
        <span style="margin-left: 15px;">TOTAL</span>
      </div>
      <br>
      <div id="ticketProdutos" v-for="det in lancamentos" :key="det._id">
        <div class="negrito">
          {{det.tipo == 'E' ? 'Recebimento' : 'Troco'}}
        </div>
        <div class="negrito">
          <!-- <span style="text-align: center">{{det.siglaMoeda}}</span>
          <span style="text-align: right; margin-left: 40px;">{{det.valor1.toFixed(2)}}</span>
          <span style="text-align: right; margin-left: 20px;">{{det.cotacao.toFixed(2)}}</span>
          <span style="text-align: right; margin-left: 10px;">{{det.valor2.toFixed(2)}}</span>   -->
          <b-row>
            <b-col cols="1">{{det.siglaMoeda}}</b-col>
            <b-col cols="4">{{det.valor1.toFixed(2)}}</b-col>
            <b-col cols="3">{{det.cotacao.toFixed(2)}}</b-col>
            <b-col cols="3"><div class="float-end ms-2">{{det.valor2.toFixed(2)}}</div></b-col>
            <!-- <b-col cols="1"><span style="text-align: center">{{det.siglaMoeda}}</span></b-col>
            <b-col cols="4"><span style="text-align: right; ">{{det.valor1.toFixed(2)}}</span></b-col>
            <b-col cols="3"><span style="text-align: right; ">{{det.cotacao.toFixed(2)}}</span></b-col>
            <b-col cols="3">{{det.valor2.toFixed(2)}}</b-col> -->
          </b-row>
        </div>
      </div>
      </section>  
    <hr>
    <section style="text-align: right" class="negrito">
        <!-- <span style="text-align: right">SUBTOTAL</span>
        <span style="text-align: right">
            subtotal
        </span> -->
        <!-- <br> -->
        <!-- <span style="text-align: right">IVA({{ 0 }}%)</span>
        <span style="text-align: right">$ 0.00</span><br> -->
        <span style="text-align: right">TOTAL</span>
        <span style="text-align: right">$ {{ totalRecebido.toFixed(2) }}</span>
    </section> 
    
    <footer>
      <div id="gracias">
        <p><b>Obrigado!</b></p>
      </div>
    </footer>
  </div>
</b-modal>
   
    <!-- fim impressão ticket -->



  </v-app>

  </Layout>


</template>


<script>
import Layout from "../../../layouts/main";
import PageHeader from "@/components/page-header";

import { ApiFin } from "./ApiFin";
import { ApiFac } from "../fac/ApiFac";
import { ApiInv } from "../inv/ApiInv";
import mensagensMixin from "../../../../mixins/mensagensMixin.js";
import moment from "moment";

export default {
  name: "Caixa",
  components: {
    Layout,
    PageHeader,
  },
  props: [],
  mixins: [mensagensMixin],
  data() {
    return {
      title: "Caixa",
      items: [
        {
          text: "Dashboard",
          href: "/home",
        },
        {
          text: "Caixa",
          active: true,
        },
      ],
      empresa: {},
      listaVendas: [],
      categorias: [],
      categoria: { id: -1, Descricao: "" },
      selected: [],
      itens: [],
      moedas: [],
      moeda: {id:0, cotacao:0, sigla:""},
      valorRecebido:0,
      valorConvertido:0,
      api: new ApiFin(),
      apiFac: new ApiFac(),
      apiInv: new ApiInv(),
      loading: false,
      search: "",
      headersVendas: [
        { text: "ID", value: "id" },
        { text: "Data", align: "start", sortable: false, value: "data" },
        { text: "Paga", align: "start", sortable: false, value: "paga" },
        { text: "Total", align: "end", sortable: false, value: "total" },
        //{ text: "Ações", value: "acoes", sortable: false },
      ],
      headersVendasSelecionadas: [
        { text: "ID", value: "id" },
        { text: "Data", align: "start", sortable: false, value: "data" },
        //{ text: "Total", align: "start", sortable: false, value: "total", formatter: (x)=>(x ? x.toFixed(2): null) },
        //{ text: "Total", align: "start", sortable: false, value: "total", formatter: this.formatCurrency },
        { text: "Total", align: "start", sortable: false, value: "total"  },
        //{ text: "Ações", value: "acoes", sortable: false },
      ],
      headersLancamentos: [
       // { text: "ID", value: "chave" },
        { text: "Ações", value: "acoes", sortable: false },
        { text: "Descricao", align: "start", sortable: false, value: "descricao" },
        { text: "Tipo", align: "start", sortable: false, value: "tipo" },
        { text: "Moeda", align: "start", sortable: false, value: "siglaMoeda" },
        { text: "Valor", align: "start", sortable: false, value: "valor1" },
        { text: "Cotação", align: "start", sortable: false, value: "cotacao" },
        { text: "Valor U$", align: "start", sortable: false, value: "valor2" },
      ],
      dialog: false,
      editedIndex: -1,
      editedItem: { id: -1, descricao: "" },
      defaultItem: { id: -1, descricao: "" },
      lancamento:{descricao:"", tipo:"", valor1:0, cotacao:0, valor2:0},
      lancamentos:[],
      mostrarBotaoGravar:false,
      dialogTicket:false,
      modalTicketShow:false,
      data: moment().format("DD/MM/YYYY"),
    };
  },

  created() {
    if (this.getEmpresaAtiva()) {
      this.listarVendas();
      this.listarMoedas();
      this.listarCategorias();
    } else {
      this.$router.push("/empresas");
    }
  },

  mounted() {},

  methods: {

    async salvar(){
      //let data = moment().format("DD/MM/YYYY")
      let data = moment().format("YYYY-MM-DD")

      let historico = this.selected.map(venda =>{
        return venda.id
      }).join()
      //console.log(historico)
      this.lancamentos.forEach(lancamento => {
        lancamento.controle = 'XXX'
        lancamento.data = data
        lancamento.empresa = this.empresa.id

        if(lancamento.tipo == 'E'){
          lancamento.descricao = 'Recebimento ' + historico
          //lancamento.id = -1
        } else if(lancamento.tipo == 'S'){
          lancamento.descricao = 'Troco ' + historico
          //lancamento.id = -1
        }

      });
      //console.log(this.lancamentos)

      const lancamentos = JSON.stringify(this.lancamentos)

      let vendas = this.selected.map(venda=>{
        return venda.id
      })

      const formulario = {
        id:-1,
        lancamentos: lancamentos,
        vendas: JSON.stringify(vendas)
      } 

      /* let y = [
        {
          "descricao":"Recebimento 130",
          "tipo":"E",
          "siglaMoeda":"",
          "valor1":12,
          "cotacao":1,
          "valor2":12,
          "siglMoeda":"U$",
          "controle":"XXX",
          "data":"2022-03-19",
          "empresa":2
          }
          ] */

     /*  const formulario = {
        id:-1,
        controle:'xxx',
        data:data,
        descricao:this.lancamentos[0].descricao,
        siglaMoeda:this.lancamentos[0].siglamoeda,
        tipo:this.lancamentos[0].tipo,
        valor1:this.lancamentos[0].valor1,
        cotacao:this.lancamentos[0].cotacao,
        valor2:this.lancamentos[0].valor2,
        empresa:this.empresa.id

      } */

      const obj = new FormData();
        for (const key in formulario) {
          obj.append(key, formulario[key]);
        }

        try {
          this.loading = true
          let resposta = await this.api.saveLancamentoCaixa(obj)
          if(typeof(resposta)=='string'){
             this.$toastr.e(`Erro ${resposta}`);
             return
          } else {
            this.$toastr.s(`Lançamento gravado!`);
            this.mostrarBotaoGravar = false
            this.listaVendas = [] //por enquanto fecha a lista de vendas mais depois que estiver filtrando só as vendas em aberto é só dar um refresh que as vendas que foram pagas ja não aparecerão
          }
          console.log(resposta)
        } catch (error) {
          this.$toastr.e(`Erro! ${error.toString()}`);
          console.log(error.toString())
        } finally{
          this.loading = false
        }

    },

    imprimir(){
      window.print()
    },

    irValorRecebido(){
      console.log('passou no ir para valor recebido')
       this.$refs.valorRecebido.focus()
       this.valorRecebido = ""
       //this.$refs.valorRecebido.select()
    },

    disableButton(){
      if(this.totalDiferenca!=0 || this.lancamentos.length == 0){
        return true
      }
      return false
    },

    enableButton(){
      if(this.totalDiferenca==0 && this.lancamentos.length!=0 && this.mostrarBotaoGravar==true){
        return true
      }
      return false
    },

    formatCurrency(){
      return "X" //+ value.toFixed(2)
    },

    formatExample(value) {
      // Just an simple example, you can do anything here
      console.log(value.toFixed(2))
      return "€" + value + ",-";
    },

    itemRowBackground: function (item) {
     return item.paga ? 'verde' : 'vermelho'
    },

    historico(){
      let novoArray = []
      let historico = 'Recebimento '
      let vendasPagas = this.selected.map(venda =>{
        return venda.id
      }).join()
      console.log(vendasPagas)
      historico += vendasPagas
      console.log(historico)
      novoArray = historico.split(' ').splice(1,1).join().split(',')
      console.log(novoArray)
    },

    getEmpresaAtiva() {
      let retorno = false;
      if (
        localStorage.getItem("empresa") !== "undefined" &&
        localStorage.getItem("empresa") !== null
      ) {
        this.empresa = JSON.parse(localStorage.getItem("empresa"));
        retorno = true;
      } else {
        this.mensagem("Selecione uma empresa!", "Atenção", "warning"); //mixin
        this.empresa = {};
        retorno = false;
        //this.$router.push('/empresas')
      }
      return retorno;
    },

    async listarMoedas() {
      console.log("======================listar moedas=======================");
      console.log("empresa moeda", this.empresa);
      let moedas = await this.api.getMoedas(-1, "", "", this.empresa.id);
      //this.moedas = moedas
      console.log(this.moedas);

      moedas.forEach((element) => {
        //this.moedas.push({ text: element.descricao, value: element.id, cotacao:element.cotacao });
        //this.moedas.push({element})
        this.moedas.push({ text: element.descricao, value: element.id, umaMoeda:element });
      });
      console.log(this.moedas);
    },

    async listarCategorias() {
      //para usar categorias com o v-select
      let cat = await this.apiInv.getCategorias();
      cat.forEach((x) => {
        this.categorias.push({ text: x.descricao, value: x.id });
      });
    },

    async listarVendas() {
      let empresa = this.empresa.id
      let vendas = await this.apiFac.getVenda(-1, "", "", empresa);
      this.listaVendas = vendas;
    },
    async iniciar() {
      try {
        this.itens = [];
        this.loading = true;
        let categorias = await this.api.getCategorias();
        this.itens = categorias;
      } catch (error) {
        //this.mensagem(error, "Houve um erro!", "error") //mixin local
        this.mensagemErro(error); //mixin global no main.js
      } finally {
        this.loading = false;
      }
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    editItem(item) {
      this.editedIndex = this.itens.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async deleteItem(item) {
      try {
        this.loading = true;
        if (
          await this.mensagemPergunta(`categoria ${item.descricao} de ${item.siglamoeda} ${item.valor1.toFixed(2)}?`, "Apagar")
        ) {
          let indice = this.lancamentos.indexOf(item)
          this.lancamentos.splice(indice, 1)
        }
      } catch (error) {
        this.$toastr.e(`Erro: ${error.toString()}`);
      } finally {
        this.loading = false;
      }
    },
    async save() {
      //const obj = this.editedItem

      const obj = new FormData();
      for (const key in this.editedItem) {
        obj.append(key, this.editedItem[key]);
      }

      try {
        this.loading = true;
        await this.api.saveCategoria(obj);
        this.close();
        this.iniciar();
        this.mensagem("Salvo com sucesso", "salvo", "success"); //mixin
      } catch (error) {
        alert(error);
      } finally {
        this.loading = false;
      }
    },
    calculaValorRecebido(){
      let cotacao = 0
      this.moedas.map(element =>{
        if(element.value === this.moeda.id){
          cotacao = element.umaMoeda.cotacao
          this.moeda.cotacao = cotacao
          this.moeda.sigla = element.umaMoeda.sigla
        }
      })
      this.valorConvertido = (this.valorRecebido / cotacao).toFixed(2)
    },
    adicionarLancamento(){

      if(isNaN(this.valorConvertido) || this.valorConvertido == 'Infinity')
      {
        this.$toastr.w(`Valor não é válido!`);
        return false
      }
      if(this.totalRecebido > this.totalSelecionado){
        this.lancamento.tipo = 'S'
        this.lancamento.descricao = "Troco de caixa"
      } else {
        this.lancamento.tipo = 'E'
        this.lancamento.descricao = "Recebimento de caixa"
      }
      this.lancamento.siglaMoeda = this.moeda.sigla
      this.lancamento.valor1 = parseFloat(this.valorRecebido)
      this.lancamento.cotacao = this.moeda.cotacao
      this.lancamento.valor2 = parseFloat(this.valorConvertido)
      //let lancamento = Object.assign({}, this.lancamento)
      //this.lancamentos.push(lancamento)
      this.lancamentos.push(this.lancamento)
      this.limparLancamento()
      this.mostrarBotaoGravar = true
      this.$refs.select_moeda.focus().select()
    },
    limparLancamento(){
      this.moeda.id = 0
      this.moeda.cotacao = 0
      this.moeda.sigla = ""
      this.valorRecebido = 0
      this.valorConvertido = 0
      this.lancamento = {}
      //lancamento:{id:1, descricao:"", siglamoeda:"", valor1:0, cotacao:0, valor2:0},
}
  },
  computed: {
    formTitle() {
      return (this.editedIndex === -1 ? "Nova " : "Editar ") + "Categoria";
    },
    totalSelecionado() {
      let t = 0;
      this.selected.map((x) => {
        t = t + x.total;
      });
      return t;
    },
    totalRecebido() {
      let t = 0;
      this.lancamentos.map((x) => {
        if(x.tipo =='E'){
          t = t + x.valor2;
        } else if(x.tipo == 'S'){
          t = t - x.valor2;
        }
      });
      return t;
    },
    totalDiferenca(){
      //let t = 0
      return (this.totalSelecionado - this.totalRecebido).toFixed(2)
    },
    totalSelecionadoPrincipaisMoedas() {
      //let cotacao = 0
      let t = {
        real:0,
        peso:0,
        guarani:0
      }
      this.moedas.map((x)=>{
        console.log(x.umaMoeda.sigla)
        if(x.umaMoeda.sigla == 'R$'){
          t.real = this.totalSelecionado * x.umaMoeda.cotacao
        }
        if(x.umaMoeda.sigla == 'G$'){
          t.guarani = this.totalSelecionado * x.umaMoeda.cotacao
        }
        if(x.umaMoeda.sigla == 'P$'){
          t.peso = this.totalSelecionado * x.umaMoeda.cotacao
        }
      })
      return t
    },
    totalDiferencaPrincipaisMoedas() {
      //let cotacao = 0
      let t = {
        real:0,
        peso:0,
        guarani:0
      }
      this.moedas.map((x)=>{
        console.log(x.umaMoeda.sigla)
        if(x.umaMoeda.sigla == 'R$'){
          t.real = this.totalDiferenca * x.umaMoeda.cotacao
        }
        if(x.umaMoeda.sigla == 'G$'){
          t.guarani = this.totalDiferenca * x.umaMoeda.cotacao
        }
        if(x.umaMoeda.sigla == 'P$'){
          t.peso = this.totalDiferenca * x.umaMoeda.cotacao
        }
      })
      return t
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
};
</script>


